import React, { useState, useEffect } from "react";
import axios from "axios";

import thumb from "../images/thumb.svg";
//api.giphy.com/v1/gifs/asdsad?api_key=lUxuo0G2Dg5qHDcas6AXWCY2RY8Y0td7
const Giphy = ({ data, onClick }) => {
  const { text, active, id, voteCount, giphyId } = data;
  const url = "https://api.giphy.com/v1/gifs/";
  const key = "?api_key=lUxuo0G2Dg5qHDcas6AXWCY2RY8Y0td7";
  //const q = `&q=${encodeURI(text)}&limit=1&offset=0&rating=G&lang=en`;
  const q = giphyId;

  const [gif, setGif] = useState({});
  useEffect(() => {
    axios
      .get(url + q + key)
      .then(response => {
        console.log(response.data.data);
        setGif(response.data.data);
      })
      .catch(error => {
        // handle error
        console.log(error);
      });
  }, [text]);

  if (gif && gif.images) {
    return (
      <div
        onClick={active ? null : () => onClick(id)}
        className={active ? "gif active" : "gif"}
      >
        <img src={gif.images.fixed_width_downsampled.url} />{" "}
        <div className="votes">
          {active ? null : <img src={thumb} />}
          {active ? null : voteCount}
        </div>
        {active ? null : <div className="inactive"></div>}
      </div>
    );
  }
  return <div></div>;
};

export default Giphy;
